          scales: {
            x: {
              display: true,
              title: {
                display: true,
                text: "Time",
                color: "#911",
                font: {
                  family: "Comic Sans MS",
                  size: 20,
                  weight: "bold",
                  lineHeight: 1.2,
                },
                padding: { top: 20, left: 0, right: 0, bottom: 0 },
