              borderColor: (ctx) => skipped(ctx, "rgb(0,0,0,0.2)"),
              borderDash: (ctx) => skipped(ctx, [6, 6]),
            },
            spanGaps: true,
          },
        ],
      };

      const config = {
        type: "line",
        data: data,
        options: {
          interaction: {
            intersect: false,
            mode: "index",
          },
          plugins: {
            title: {
              display: true,
              text: "[DLP] LUNA/BUSD (Real)",
            },
            tooltip: {
              callbacks: {
                // footer: footer,
              },
            },
            // legend: {
            //   labels: {
            //     padding: 50,
            //   },
            // },
          },
          scales: {
            x: {
              display: true,
              title: {
                display: true,
                text: "Operating Time (H)",
                color: "#911",
                font: {
                  family: "Comic Sans MS",
                  size: 20,
                  weight: "bold",
                  lineHeight: 1.2,
                },
                padding: { top: 20, left: 0, right: 0, bottom: 0 },
